import React, {useEffect} from "react";
import WOW from "wowjs";
import {useTranslation} from "react-i18next";

const Partners = () => {
    // wow js
    useEffect(() => {
        new WOW.WOW().init();
    }, []);
    const {t} = useTranslation()
    
    return (
        <div>
            <div className="head_content">
                <div className="main_text_box sub co1">
                    <h3 className="wow fadeInUp" data-wow-delay='.2s'>{t('partner_section1_tit1')}</h3>
                </div>
            </div>
            <div className="section">
                <div className="sub_wrap">
                    <div className="flex partner_box">
                        <img className="wow fadeInLeft" data-wow-offset="400"
                             src={process.env.PUBLIC_URL + '/assets/img/partner_section1.png'}
                             alt=""/>
                        <div className="main_text_box">
                            <h3 className="wow fadeIn" data-wow-offset="200"
                                data-wow-delay='.4s'>{t('partner_section2_tit1')}</h3>
                            <p className="wow fadeIn" data-wow-offset="200"
                               data-wow-delay='.8s'>{t('partner_section2_p1')}</p>
                        </div>
                    </div>
                </div>
            </div>
            <div className="section">
                <div className="sub_wrap bg_partner">
                    <div className="main_text_box">
                        <h3 className="wow fadeIn" data-wow-offset="200"
                            data-wow-delay='.4s'>{t('partner_section3_tit1')}</h3>
                    </div>
                    <ul className="partner_card">
                        <li className="wow fadeIn" data-wow-delay='.8s'>
                            <img src={process.env.PUBLIC_URL + '/assets/img/partner_1.png'} alt=""/>
                            <strong className="tit">{t('partner_card1_tit1')}</strong>
                            <p className="info">{t('partner_card1_p1')}</p>
                            <span className="tip">{t('partner_card1_span1')}</span>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='1.2s'>
                            <img src={process.env.PUBLIC_URL + '/assets/img/partner_2.png'} alt=""/>
                            <strong className="tit">{t('partner_card1_tit2')}</strong>
                            <p className="info">{t('partner_card1_p2')}</p>
                            <span className="tip">{t('partner_card1_span2')}</span>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='1.6s'>
                            <img src={process.env.PUBLIC_URL + '/assets/img/partner_3.png'} alt=""/>
                            <strong className="tit">{t('partner_card1_tit3')}</strong>
                            <p className="info">{t('partner_card1_p3')}</p>
                            <span className="tip">{t('partner_card1_span3')}</span>
                        </li>
                    </ul>
                    <div className="main_text_box">
                        <h3 className="wow fadeIn" data-wow-offset="200"
                            data-wow-delay='.4s'>{t('partner_section3_tit2')}</h3>
                    </div>
                    <ul className="partner_card">
                        <li className="wow fadeInLeft" data-wow-delay='.8s'>
                            <img src={process.env.PUBLIC_URL + '/assets/img/partner_section1.png'} alt=""/>
                            <strong className="tit">{t('partner_card2_tit1')}</strong>
                            <p className="info">{t('partner_card2_p1')}</p>
                            <span className="tip">{t('partner_card2_span1')}</span>
                        </li>
                        <li className="wow fadeInRight" data-wow-delay='1.2s'>
                            <img src={process.env.PUBLIC_URL + '/assets/img/partner_section1.png'} alt=""/>
                            <strong className="tit">{t('partner_card2_tit2')}</strong>
                            <p className="info">{t('partner_card2_p2')}</p>
                            <span className="tip">{t('partner_card2_span2')}</span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Partners;