import ReactFullpage from '@fullpage/react-fullpage'
import {useEffect, useState} from "react";
import {Link, NavLink} from "react-router-dom";
import WOW from "wowjs";
import {useTranslation} from "react-i18next";

// swiper 사용 npm install swiper@6.8.4
import {Swiper, SwiperSlide} from "swiper/react";
import SwiperCore, {Navigation, Autoplay, EffectFade} from "swiper";

import "swiper/swiper.min.css";
import "swiper/components/navigation/navigation.min.css";
import "swiper/components/effect-fade/effect-fade.min.css"

{/*<img src={process.env.PUBLIC_URL + '/assets/img/banner_main.png'} alt=""/> 이미지는 이런식으로 */
}

const Home = () => {

    //swiper
    const [swiper, setSwiper] = useState(null);

    SwiperCore.use([Navigation, Autoplay, EffectFade])

    const swiperParams = {
        onSwiper: setSwiper,
        // navigation: {
        //   nextEl: '.swiper-button-next',
        //   prevEl: '.swiper-button-prev',
        // },
        // 몇초 뒤 넘어감
        autoplay: {
            delay: 3500,
            // 마우스 오버시 정지기능
            // false로 설정하면 스와이프 후 자동 재생이 비활성화 되지 않음
            disableOnInteraction: false,
        },
        // 마우스 터치 넘기기 막음
        allowTouchMove: false,
        // 반복 (마지막에서 첫번째로 돌아가게)
        loop: true,
        // 넘어가는 속도
        speed: 500,
        effect: 'fade',
        // fade 효과일 때 슬라이드 이미지가 쌓이는 느낌 그림자 이미지가 점점 진하게 드리움 위 현상을 막기 위해 아래 옵션 추가
        fadeEffect: {
            crossFade: true
        },
        // swiper js display:none 상태에서 작동하지 않을 때 display:none 요소에서 block이 될 때 슬라이드는 작동되지 않는다. 아래 옵션값을 넣어 초기화 해준다.
        observer: true,
        observeParents: true,
    }

    // 번역
    const {t} = useTranslation()

    // wow js
    useEffect(() => {
        new WOW.WOW().init();
    }, []);

    // swiper 안에 wowjs 작동
    function slidewow() {
        new WOW.WOW({
            boxClass: 'slidewow'
        }).init();
    }

    // 마우스 빼면 이미지 변경
    function backgroundLeaveOut() {
        document.querySelector("#bg_change").style.backgroundImage = "url(/assets/img/bg_main_section3.png)"
    }

    function changeBackground1() {
        document.querySelector("#bg_change").style.backgroundImage = "url(/assets/img/bg_main_section3.png)"
    }

    function changeBackground2() {
        document.querySelector("#bg_change").style.backgroundImage = "url(/assets/img/bg_main_section3_2.png)"
    }

    function changeBackground3() {
        document.querySelector("#bg_change").style.backgroundImage = "url(/assets/img/bg_main_section3.png)"
    }

    function changeBackground4() {
        document.querySelector("#bg_change").style.backgroundImage = "url(/assets/img/bg_main_section3_2.png)"
    }

    return (
        <ReactFullpage
            //fullpage options
            scrollBar={true}
            licenseKey={'A761CE4B-782C4180-9B083DCF-7459F17A'}
            responsiveWidth={1280}
            scrollingSpeed={700} /* Options here */
            render={({state, fullpageApi}) => {

                return (
                    <ReactFullpage.Wrapper>
                        <div className="section">
                            {/*<img src={process.env.PUBLIC_URL + '/assets/img/banner_main.png'} alt=""/> 이미지는 이런식으로 */}
                            <div className="section_wrap">
                                <Swiper {...swiperParams} ref={setSwiper}
                                        onSlideChange={() => slidewow()}>
                                    <SwiperSlide className="swiper_layout bg_main1">
                                        <div className="main_text_box co1 pt0">
                                            <h3 className="slidewow fadeInUp"
                                                data-wow-delay='.4s'>{t('main_section1_tit1')}</h3>
                                            <p className="slidewow fadeInUp"
                                               data-wow-delay='1.0s'>{t('main_section1_p1')}</p>
                                        </div>
                                    </SwiperSlide>
                                    <SwiperSlide className="swiper_layout bg_main3">
                                        <div className="main_text_box co1 pt0">
                                            <h3 className="slidewow fadeInUp"
                                                data-wow-delay='.4s'>{t('main_section1_tit2')}</h3>
                                            <p className="slidewow fadeInUp"
                                               data-wow-delay='.8s'>{t('main_section1_p2')}</p>
                                        </div>
                                    </SwiperSlide>
                                </Swiper>
                                {/*<div className="swiper-button-next"></div>*/}
                                {/*<div className="swiper-button-prev"></div>*/}
                                {/* 매개변수 fullpageapi용으로 가려놓음 */}
                                <button className="sr-only" onClick={() => fullpageApi.moveTo(1, 0)}>
                                    {/*fullpageApi.moveSectionDown()*/}
                                    Click me to move down
                                </button>
                                {/* 가려놓음 끝 */}
                            </div>
                        </div>
                        <div className="section bg_main2">
                            <div className="section_wrap">
                                <div className="main_text_box">
                                    <h3 className="wow fadeIn" data-wow-delay='.2s'>{t('main_section2_tit')}</h3>
                                    <p className="wow fadeIn" data-wow-delay='.4s'>{t('main_section2_p')}</p>
                                </div>
                                <ul className="card_list three">
                                    <li className="wow fadeInUp" data-wow-delay='.4s'>
                                        <div className="img"></div>
                                        <div className="grid">
                                            <strong className="strong">{t('main_section2_list_tit1')}</strong>
                                            <p>{t('main_section2_list_p1')}</p>
                                        </div>
                                    </li>
                                    <li className="wow fadeInUp" data-wow-delay='.6s'>
                                        <div className="img"></div>
                                        <div className="grid">
                                            <strong className="strong">{t('main_section2_list_tit2')}</strong>
                                            <p>{t('main_section2_list_p2')}</p>
                                        </div>
                                    </li>
                                    <li className="wow fadeInUp" data-wow-delay='.8s'>
                                        <div className="img"></div>
                                        <div className="grid">
                                            <strong className="strong">{t('main_section2_list_tit3')}</strong>
                                            <p>{t('main_section2_list_p3')}</p>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div id="bg_change" className="section bg_main3">
                            <div className="section_wrap">
                                <div className="main_text_box co1">
                                    <h3 className="wow fadeIn" data-wow-delay='.2s'>{t('main_section3_tit')}</h3>
                                </div>
                            </div>
                            <ul className="card_list four">
                                <li className="wow fadeIn" data-wow-delay='.4s' onMouseOver={changeBackground1}
                                    onMouseLeave={backgroundLeaveOut}>
                                    <span className="number">01</span>
                                    <div className="img"/>
                                    <strong className="strong">{t('main_section3_list_tit1')}</strong>
                                    <p>{t('main_section3_list_p1')}</p>
                                </li>
                                <li className="wow fadeIn" data-wow-delay='.8s' onMouseOver={changeBackground2}
                                    onMouseLeave={backgroundLeaveOut}>
                                    <span className="number">02</span>
                                    <div className="img"/>
                                    <strong className="strong">{t('main_section3_list_tit2')}</strong>
                                    <p>{t('main_section3_list_p2')}</p>
                                </li>
                                <li className="wow fadeIn" data-wow-delay='1.2s' onMouseOver={changeBackground3}
                                    onMouseLeave={backgroundLeaveOut}>
                                    <span className="number">03</span>
                                    <div className="img"/>
                                    <strong className="strong">{t('main_section3_list_tit3')}</strong>
                                    <p>{t('main_section3_list_p3')}</p>
                                </li>
                                <li className="wow fadeIn" data-wow-delay='1.6s' onMouseOver={changeBackground4}
                                    onMouseLeave={backgroundLeaveOut}>
                                    <span className="number">04</span>
                                    <div className="img"/>
                                    <strong className="strong">{t('main_section3_list_tit4')}</strong>
                                    <p>{t('main_section3_list_p4')}</p>
                                </li>
                            </ul>
                        </div>
                        <div className="section">
                            <div className="section_wrap mo_mh850">
                                <div className="layout_box flex">
                                    <div className="left">
                                        <img className="img_ab1 wow fadeInDown" data-wow-delay='.2s'
                                             data-wow-offset="400"
                                             src={process.env.PUBLIC_URL + '/assets/img/main_section4_top.png'} alt=""/>
                                        <img className="img_ab2 wow fadeIn" data-wow-delay='1.0s' data-wow-offset="200"
                                             src={process.env.PUBLIC_URL + '/assets/img/main_section4_right.png'}
                                             alt=""/>
                                        <img className="img_ab3 wow fadeInUp" data-wow-delay='.4s' data-wow-offset="200"
                                             src={process.env.PUBLIC_URL + '/assets/img/main_section4_btm.png'} alt=""/>
                                        <img className="img_ab4 wow fadeIn" data-wow-delay='1.2s' data-wow-offset="200"
                                             src={process.env.PUBLIC_URL + '/assets/img/main_section4_left.png'}
                                             alt=""/>
                                    </div>
                                    <div className="right">
                                        <div className="main_text_box">
                                            <h3 className="strong">{t('main_section4_tit1')}</h3>
                                            <p>{t('main_section4_p1')}</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="section bg_main4">
                            <div className="section_wrap">
                                <div className="main_text_box co1 pt0">
                                    <h3 className="wow fadeIn"
                                        data-wow-delay='.2s'>{t('main_section5_tit1')}</h3>
                                </div>
                                <ul className="card_list four type2">
                                    <li className="wow fadeIn" data-wow-delay='.4s' onMouseOver={changeBackground1}
                                        onMouseLeave={backgroundLeaveOut}>
                                        <div className="img"></div>
                                        <strong className="strong">{t('main_section5_list_tit1')}</strong>
                                    </li>
                                    <li className="wow fadeIn" data-wow-delay='.8s' onMouseOver={changeBackground2}
                                        onMouseLeave={backgroundLeaveOut}>
                                        <div className="img"></div>
                                        <strong className="strong">{t('main_section5_list_tit2')}</strong>
                                    </li>
                                    <li className="wow fadeIn" data-wow-delay='1.2s' onMouseOver={changeBackground3}
                                        onMouseLeave={backgroundLeaveOut}>
                                        <div className="img"></div>
                                        <strong className="strong">{t('main_section5_list_tit3')}</strong>
                                    </li>
                                    <li className="wow fadeIn" data-wow-delay='1.6s' onMouseOver={changeBackground4}
                                        onMouseLeave={backgroundLeaveOut}>
                                        <div className="img"></div>
                                        <strong className="strong">{t('main_section5_list_tit4')}</strong>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div className="section bg_main5">
                            <div className="section_wrap">
                                <div className="main_text_box ">
                                    <h3 className="wow fadeIn"
                                        data-wow-delay='.2s'>{t('main_section6_tit1')}</h3>
                                </div>
                                <ul className="partner">
                                    <li className="wow fadeIn" data-wow-delay='.6s'><img
                                        src={process.env.PUBLIC_URL + '/assets/img/partner_maekyung.png'}
                                        alt=""/>
                                    </li>
                                    <li className="wow fadeIn" data-wow-delay='.8s'><img
                                        src={process.env.PUBLIC_URL + '/assets/img/partner_splabs.png'}
                                        alt=""/>
                                    </li>
                                    <li className="wow fadeIn" data-wow-delay='1.0s'><img
                                        src={process.env.PUBLIC_URL + '/assets/img/partner_spexchange.png'}
                                        alt=""/></li>
                                    <li className="wow fadeIn" data-wow-delay='1.2s'><img
                                        src={process.env.PUBLIC_URL + '/assets/img/partner_rarts.png'} alt=""/>
                                    </li>
                                    <li className="wow fadeIn" data-wow-delay='1.4s'><img
                                        src={process.env.PUBLIC_URL + '/assets/img/partner_gramming.png'}
                                        alt=""/>
                                    </li>
                                    <li className="wow fadeIn" data-wow-delay='1.6s'><img
                                        src={process.env.PUBLIC_URL + '/assets/img/partner_aquapnix.png'}
                                        alt=""/>
                                    </li>
                                </ul>
                                <div className="text_box">
                                    <p className="type2 wow fadeInUp" data-wow-delay='1.8s'>{t('main_section6_p1')}</p>
                                    <Link to="/contact" className="btn1 mt30 wow fadeInUp"
                                          data-wow-delay='2.0s'>{t('main_section6_button1')}
                                    </Link>
                                </div>
                            </div>
                        </div>
                        <div className="section footer fp-auto-height">
                            <div className="section_wrap">
                                <div className="flex">
                                    <div className="left wow fadeInLeft" data-wow-delay='.4s'>
                                        <div className="logo_box">
                                            <img className="img_ab1"
                                                 src={process.env.PUBLIC_URL + '/assets/img/logo_footer.png'}
                                                 alt=""/>
                                            <p>From the labs behind Red Kite launchpad and several NFT games</p>
                                        </div>
                                        <ul className="info">
                                            <li>{t('footer_1')}</li>
                                            <li>{t('footer_2')}</li>
                                            <li>{t('footer_3')}</li>
                                            <li>{t('footer_4')}</li>
                                        </ul>
                                        <p>Copyright © 2022 Nettiss All reserved.</p>
                                    </div>
                                    <div className="right wow fadeInRight" data-wow-delay='.8s'>
                                        <ul>
                                            <li>
                                                <a href="https://facebook.com/" target="_blank"
                                                   rel="noopener noreferrer">
                                                    <img className="img_ab1"
                                                         src={process.env.PUBLIC_URL + '/assets/img/ico_facebook.png'}
                                                         alt=""/>
                                                </a>
                                            </li>
                                            <li>
                                                <a href="https://kakao.com/" target="_blank" rel="noopener noreferrer">
                                                    <img className="img_ab1"
                                                         src={process.env.PUBLIC_URL + '/assets/img/ico_kakao.png'}
                                                         alt=""/>
                                                </a>
                                            </li>
                                            <li>
                                                <a href="https://instagram.com/" target="_blank"
                                                   rel="noopener noreferrer">
                                                    <img className="img_ab1"
                                                         src={process.env.PUBLIC_URL + '/assets/img/ico_instagram.png'}
                                                         alt=""/>
                                                </a>
                                            </li>
                                            <li>
                                                <a href="https://twitter.com/" target="_blank"
                                                   rel="noopener noreferrer">
                                                    <img className="img_ab1"
                                                         src={process.env.PUBLIC_URL + '/assets/img/ico_twitter.png'}
                                                         alt=""/>
                                                </a>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </ReactFullpage.Wrapper>
                );
            }}
        />
    )


}

export default Home;