import {useEffect, useState, useRef} from "react";
import WOW from "wowjs";
import {useTranslation} from "react-i18next";
import {useForm} from "react-hook-form";


const Contact = () => {
    // wow js
    useEffect(() => {
        new WOW.WOW().init();
    }, []);
    const {t} = useTranslation()

    const {register, watch, reset, handleSubmit, formState: {errors},} = useForm({
        // 폼 기본값 설정
        defaultValues: {
            contactChk: false,
            name: "",
            email: "",
            tel: ""
        }
    });
    const [errorFromSubmit, setErrorFromSubmit] = useState("");
    const [loading, setLoading] = useState(false);
    const password = useRef(); //비밀번호 재확인 할떄 필요
    password.current = watch("password"); //name password인 element 관찰

    const onError = (error) => {
        console.log(error);
        console.log('에러메세지 입니다. 제출 되지 않습니다.');
    };

    const onSubmit = async (data) => {
        console.log(data);
        console.log('전송이 완료되었습니다.');
        reset({
            contactChk: false,
            name: "",
            email: "",
            tel: "",
            textarea: ""
        });
        alert(t('contact_submmit'))
    }

    return (
        <div>
            <div className="head_content">
                <div className="main_text_box sub co1">
                    <h3 className="wow fadeInUp" data-wow-delay='.2s'>{t('contact_section1_tit1')}</h3>
                </div>
            </div>
            {/*//제출하면 유효성 검사 시작*/}
            <form onSubmit={handleSubmit(onSubmit, onError)}>
                <div className="section">
                    <div className="sub_wrap">
                        <div className="main_text_box">
                            <h3 className="wow fadeInUp" data-wow-delay='.2s'
                                data-wow-offset="400">{t('contact_section2_tit1')}</h3>
                        </div>
                        <div className="contact_info wow fadeIn" data-wow-delay='.6s' data-wow-offset="200">
                            <p>{t('contact_section2_p1')}</p>
                            <span className="chk_box">
                                <input id="contactChk" name="contactChk" type="checkbox"
                                       {...register("contactChk", {
                                           required: true,
                                       })}/>
                                <label htmlFor="contactChk">{t('contact_section2_info1')}</label>
                            </span>
                            {errors.contactChk && errors.contactChk.type === "required" && (
                                <p className="form_tip">{t('contact_section2_tip1')}</p>
                            )}
                        </div>
                    </div>
                </div>
                <div className="section">
                    <div className="sub_wrap bg_contact">
                        <div className="form_box">
                            <div className="flex">
                                <div data-wow-delay='.2s' className="wow fadeIn input_box">
                                    <input name="name" autoComplete="off"
                                           placeholder={t('contact_input1')} {...register("name", {
                                        required: true,
                                        maxLength: 20
                                    })}
                                    />
                                    {errors.name && errors.name.type === "required" && (
                                        <p className="form_tip">{t('contact_section2_tip8')}</p>
                                    )}
                                    {errors.name && errors.name.type === "maxLength" && (
                                        <p className="form_tip">{t('contact_section2_tip9')}</p>
                                    )}
                                </div>
                                <div data-wow-delay='0.6s' className="wow fadeIn input_box">
                                    <input name="email" autoComplete="off" type="email"
                                           placeholder={t('contact_input3')} {...register("email", {
                                        required: true,
                                        pattern: /^\S+@\S+$/i,
                                    })}/>
                                    {errors.email && errors.email.type === 'required' && (
                                        <p className="form_tip">{t('contact_section2_tip2')}</p>
                                    )}
                                    {errors.email && errors.email.type === "pattern" && (
                                        <p className="form_tip">{t('contact_section2_tip3')}</p>
                                    )}
                                </div>
                                <div data-wow-delay='1.0s' className="wow fadeIn input_box">
                                    <input name="tel" type="tel" autoComplete="off"
                                           placeholder={t('contact_input2')} {...register("tel", {
                                        required: false, minLength: 6, maxLength: 15,
                                        // pattern: /^01(?:0|1|[6-9])-(?:\d{3}|\d{4})-\d{4}$/, // '-' 같이 입력시
                                        pattern: /^01(?:0|1|[6-9])(?:\d{3}|\d{4})\d{4}$/
                                    })}/>
                                    {/*{errors.tel && errors.tel.type === "required" && (*/}
                                    {/*    <p className="form_tip">휴대폰 번호를 입력해 주세요.</p>*/}
                                    {/*)}*/}
                                    {/*{errors.tel && errors.tel.type === "minLength" && (*/}
                                    {/*    <p className="form_tip">최소 6자리 이상 입력해주세요.</p>*/}
                                    {/*)}*/}
                                    {/*{errors.tel && errors.tel.type === "maxLength" && (*/}
                                    {/*    <p className="form_tip">최대 15자리를 넘길 수 없습니다.</p>*/}
                                    {/*)}*/}
                                    {errors.tel && errors.tel.type === "pattern" && (
                                        <p className="form_tip">{t('contact_section2_tip4')}</p>
                                    )}
                                </div>
                            </div>
                            <div data-wow-delay='1.4s' className="wow fadeIn input_box">
                                <textarea name="textarea" type="text"
                                          placeholder={t('contact_input4')} {...register("textarea", {
                                    required: true, minLength: 5, maxLength: 3000
                                })}/>
                                {errors.textarea && errors.textarea.type === "required" && (
                                    <p className="form_tip">{t('contact_section2_tip5')}</p>
                                )}
                                {errors.textarea && errors.textarea.type === "minLength" && (
                                    <p className="form_tip">{t('contact_section2_tip6')}</p>
                                )}
                                {errors.textarea && errors.textarea.type === "maxLength" && (
                                    <p className="form_tip">{t('contact_section2_tip7')}</p>
                                )}
                            </div>
                            {errorFromSubmit && <p className="form_tip">{errorFromSubmit}</p>}
                            <button data-wow-delay='.6s' className="wow fadeInUp btn1" type="submit"
                                    disabled={loading}>{t('contact_input5')}</button>
                        </div>
                    </div>
                </div>
            </form>
        </div>
    )
}

export default Contact;