import React, {useEffect} from "react";
import WOW from "wowjs";
import {useTranslation} from "react-i18next";

const Metaverse = () => {
    // wow js
    useEffect(() => {
        new WOW.WOW().init();
    }, []);
    const {t} = useTranslation()
    return (
        <div>
            <div className="head_content">
                <div className="main_text_box sub co1">
                    <h3 className="wow fadeInUp" data-wow-delay='.2s'>{t('metaverse_section1_tit1')}</h3>
                    <p className="wow fadeInUp" data-wow-delay='.4s'>{t('metaverse_section1_p1')}</p>
                </div>
            </div>
            <div className="section">
                <div className="sub_wrap">
                    <div className="main_text_box">
                        <h3 className="wow fadeIn" data-wow-delay='.8s'>{t('metaverse_section2_tit1')}</h3>
                        <p className="wow fadeIn" data-wow-delay='1.2s'>{t('metaverse_section2_p1')}</p>
                    </div>
                    <div className="flex metaverse">
                        <div className="left wow fadeInLeft" data-wow-delay='.2s' data-wow-offset="200">
                            <img src={process.env.PUBLIC_URL + '/assets/img/metaverse_section1.png'} alt=""/>
                        </div>
                        <ul className="right">
                            <li className="wow fadeIn" data-wow-delay='.6s' data-wow-offset="200">
                                <div className="img_box">
                                    <img src={process.env.PUBLIC_URL + '/assets/img/metaverse_section1_1.png'} alt=""/>
                                </div>
                                <strong>{t('metaverse_section2_list_tit1')}</strong>
                                <p>{t('metaverse_section2_list_p1')}</p>
                            </li>
                            <li className="wow fadeIn" data-wow-delay='1.0s' data-wow-offset="200">
                                <div className="img_box">
                                    <img src={process.env.PUBLIC_URL + '/assets/img/metaverse_section1_2.png'} alt=""/>
                                </div>
                                <strong>{t('metaverse_section2_list_tit2')}</strong>
                                <p>{t('metaverse_section2_list_p2')}</p>
                            </li>
                            <li className="wow fadeIn" data-wow-delay='1.4s' data-wow-offset="200">
                                <div className="img_box">
                                    <img src={process.env.PUBLIC_URL + '/assets/img/metaverse_section1_3.png'} alt=""/>
                                </div>
                                <strong>{t('metaverse_section2_list_tit3')}</strong>
                                <p>{t('metaverse_section2_list_p3')}</p>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <div className="section">
                <div className="sub_wrap bg_metaverse">
                    <div className="main_text_box sub co1 pt100">
                        <h3 className="wow fadeInUp" data-wow-delay='.2s'>{t('metaverse_section3_tit1')}</h3>
                    </div>
                    <ul className="card_list four type3 type_plus">
                        <li className="wow fadeIn" data-wow-delay='.4s'>
                            <div className="img"/>
                            <p>{t('metaverse_section3_list_p1')}</p>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='.8s'>
                            <div className="img"/>
                            <p>{t('metaverse_section3_list_p2')}</p>
                        </li>
                    </ul>
                </div>
            </div>
            <div className="section">
                <div className="sub_wrap">
                    <div className="main_text_box">
                        <h3 className="wow fadeIn" data-wow-delay='.2s'>{t('metaverse_section4_tit1')}</h3>
                    </div>
                    <ul className="card_list six type2">
                        <li className="wow fadeIn" data-wow-delay='.2s' data-wow-offset="200">
                            <strong className="tit">{t('metaverse_section4_list1')}</strong>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='.4s' data-wow-offset="200">
                            <strong className="tit">{t('metaverse_section4_list2')}</strong>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='.6s' data-wow-offset="200">
                            <strong className="tit">{t('metaverse_section4_list3')}</strong>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='.8s' data-wow-offset="200">
                            <strong className="tit">{t('metaverse_section4_list4')}</strong>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='1.0s' data-wow-offset="200">
                            <strong className="tit">{t('metaverse_section4_list5')}</strong>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='1.2s' data-wow-offset="200">
                            <strong className="tit">{t('metaverse_section4_list6')}</strong>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='1.4s'>
                            <strong className="tit">{t('metaverse_section4_list7')}</strong>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='1.6s'>
                            <strong className="tit">{t('metaverse_section4_list8')}</strong>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='1.8s'>
                            <strong className="tit">{t('metaverse_section4_list9')}</strong>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='2.0s'>
                            <strong className="tit">{t('metaverse_section4_list10')}</strong>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='2.2s'>
                            <strong className="tit">{t('metaverse_section4_list11')}</strong>
                        </li>
                        <li className="wow fadeIn" data-wow-delay='2.4s'>
                            <strong className="tit">{t('metaverse_section4_list12')}</strong>
                        </li>
                    </ul>
                </div>
            </div>
            <div className="section">
                <div className="sub_wrap bg_metaverse2 ">
                    <div className="main_text_box sub co1 pt100">
                        <h3 className="wow fadeInUp" data-wow-delay='.2s'>{t('metaverse_section5_tit1')}</h3>
                    </div>
                    <ul className="service_list">
                        <li className="wow fadeInLeft" data-wow-delay='.4s'>
                            <img src={process.env.PUBLIC_URL + '/assets/img/service_list1.png'} alt=""/>
                            <p>{t('metaverse_section5_list1')}</p>
                        </li>
                        <li className="wow fadeInLeft" data-wow-delay='.7s'>
                            <img src={process.env.PUBLIC_URL + '/assets/img/service_list2.png'} alt=""/>
                            <p>{t('metaverse_section5_list2')}</p>
                        </li>
                        <li className="wow fadeInLeft" data-wow-delay='1.0s'>
                            <img src={process.env.PUBLIC_URL + '/assets/img/service_list3.png'} alt=""/>
                            <p>{t('metaverse_section5_list3')}</p>
                        </li>
                        <li className="wow fadeInLeft" data-wow-delay='1.3s'>
                            <img src={process.env.PUBLIC_URL + '/assets/img/service_list4.png'} alt=""/>
                            <p>{t('metaverse_section5_list4')}</p>
                        </li>
                        <li className="wow fadeInLeft" data-wow-delay='1.6s'>
                            <img src={process.env.PUBLIC_URL + '/assets/img/service_list5.png'} alt=""/>
                            <p>{t('metaverse_section5_list5')}</p>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Metaverse;