import {Link, NavLink} from "react-router-dom";
import {useState, useEffect} from "react";
import {useTranslation} from 'react-i18next'

const Header = () => {

    const [scrollPosition, setScrollposition] = useState(0);
    const updateScroll = () => {
        setScrollposition(window.scrollY || document.documentElement.scrollTop);
    }
    // const scrollPosition55 = window.scrollY || document.documentElement.scrollTop;
    // console.log(scrollPosition55);

    useEffect(() => {
        window.addEventListener('scroll', updateScroll);
    }, []);
    const {t, i18n} = useTranslation()
    const changelanguageToKo = (e) => {
        i18n.changeLanguage('ko');
        e.target.classList.add('on');
        e.target.nextSibling.classList.remove('on');
    }
    const changelanguageToEn = (e) => {
        i18n.changeLanguage('en');
        e.target.classList.add('on');
        e.target.previousSibling.classList.remove('on');
    }

    function menuClose() {
        document.querySelector("#menu_open").classList.remove("open")
        document.querySelector(".close_menu").classList.remove("open")
    }

    function menuOpen() {
        document.querySelector("#menu_open").classList.add("open")
        document.querySelector(".close_menu").classList.add("open")
    }

    return (
        <header className={scrollPosition < 100 ? "header" : "header on"}>
            {/*<header className="header">*/}
            <button className="close_menu" onClick={menuClose}></button>
            <div className="header_wrap">
                <h1 className="logo">
                    <NavLink className={(navData) => navData.isActive ? "active" : ""}
                             to="/">
                        <img src={process.env.PUBLIC_URL + '/assets/img/logo.png'} alt=""/>
                    </NavLink>
                </h1>
                <div className="mo_box" onClick={menuOpen}>
                    <div className="mo_filter" id="menu_open">
                        <div className="mo">
                            <ul className="nav">
                                <li>
                                    <NavLink className={(navData) => navData.isActive ? "active" : ""}
                                             to="/">
                                        {t('nav1')}
                                    </NavLink>
                                </li>
                                <li>
                                    <NavLink className={(navData) => navData.isActive ? "active" : ""}
                                             to="/company">
                                        {t('nav2')}
                                    </NavLink>
                                </li>
                                <li>
                                    <NavLink className={(navData) => navData.isActive ? "active" : ""}
                                             to="/platform">
                                        {t('nav3')}
                                    </NavLink>
                                </li>
                                <li>
                                    <NavLink className={(navData) => navData.isActive ? "active" : ""}
                                             to="/metaverse">
                                        {t('nav4')}
                                    </NavLink>
                                </li>
                                <li>
                                    <NavLink className={(navData) => navData.isActive ? "active" : ""}
                                             to="/partners">
                                        {t('nav5')}
                                    </NavLink>
                                </li>
                                <li>
                                    <NavLink className={(navData) => navData.isActive ? "active" : ""}
                                             to="/contact">
                                        {t('nav6')}
                                    </NavLink>
                                </li>
                            </ul>
                            <div className="lang">
                                <button className="on" onClick={changelanguageToKo}>KOR</button>
                                <button onClick={changelanguageToEn}>ENG</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </header>
    )
}

export default Header