import {useTranslation} from "react-i18next";
import React, {useEffect, useState} from "react";
import WOW from "wowjs";
import {MapMarker, Map, CustomOverlayMap, ZoomControl, MapTypeControl, MapTypeId} from "react-kakao-maps-sdk";

const Company = () => {
    // wow js
    useEffect(() => {
        new WOW.WOW().init();
    }, []);
    const {t} = useTranslation()

    const {kakao} = window;
    const [isOpen, setIsOpen] = useState(true)
    // true 면 오픈된 상태로 보이고, false 면 오픈 되지 않은 상태로 열림.

    const markerPosition = {
        lat: 37.299241,
        lng: 127.042792,
    }

    return (
        <div>
            <div className="head_content">
                <div className="main_text_box sub co1">
                    <h3 className="wow fadeInUp" data-wow-delay='.2s'>{t('company_section1_tit1')}</h3>
                    <p className="wow fadeInUp" data-wow-delay='.4s'>{t('company_section1_p1')}</p>
                </div>
            </div>
            <div className="section">
                <div className="sub_wrap">
                    <div className="main_text_box">
                        <h3 className="wow fadeInUp" data-wow-delay='.0s'
                            data-wow-offset="400">{t('company_section2_tit1')}</h3>
                        <p className="wow fadeInUp" data-wow-delay='.2s'
                           data-wow-offset="400">{t('company_section2_p1')}</p>
                    </div>
                    <div className="info_wrap">
                        <ul className="company_info">
                            <li className="wow fadeIn" data-wow-delay='.4s' data-wow-offset="100">
                                <img src={process.env.PUBLIC_URL + '/assets/img/company_1.png'} alt=""/>
                                <span>{t('company_section2_list1')}</span>
                            </li>
                            <li className="wow fadeIn" data-wow-delay='.8s' data-wow-offset="200">
                                <img src={process.env.PUBLIC_URL + '/assets/img/company_2.png'} alt=""/>
                                <span>{t('company_section2_list2')}</span>
                            </li>
                            <li className="wow fadeIn" data-wow-delay='1.2s' data-wow-offset="200">
                                <img src={process.env.PUBLIC_URL + '/assets/img/company_3.png'} alt=""/>
                                <span>{t('company_section2_list3')}</span>
                            </li>
                            <li className="wow fadeIn" data-wow-delay='1.6s' data-wow-offset="100">
                                <img src={process.env.PUBLIC_URL + '/assets/img/company_4.png'} alt=""/>
                                <span>{t('company_section2_list4')}</span>
                            </li>
                            <li className="in_company wow fadeIn" data-wow-delay='2.0s' data-wow-offset="100">
                                <span>{t('company_section2_tit2_1')}</span>
                                <strong>"{t('company_section2_tit2_2')}"</strong>
                            </li>
                        </ul>

                        <p className="company_title wow fadeInUp" data-wow-delay='3.2s'>
                            {t('company_section2_p2')}
                        </p>
                    </div>
                </div>
            </div>
            <div className="section bg_co1">
                <div className="sub_wrap">
                    <div className="main_text_box">
                        <h3 className="wow fadeInUp" data-wow-delay='.6s'>{t('company_section3_tit1')}</h3>
                    </div>
                    <div className="map_wrap wow fadeIn" data-wow-delay='1.0s'>
                        {/*<RemovableCustomOverlayStyle/>*/}
                        <Map // 지도를 표시할 Container
                            id={`map`}
                            center={{
                                // 지도의 중심좌표
                                lat: 37.299241,
                                lng: 127.042792,
                            }}
                            style={{
                                // 지도의 크기
                                width: "100%",
                                height: "610px",
                            }}
                            level={3} // 지도의 확대 레벨
                        >
                            <MapMarker position={markerPosition} onClick={() => setIsOpen(true)}/>
                            {isOpen && (
                                <CustomOverlayMap position={markerPosition}>
                                    <div className="map_box">
                                        <div className="info">
                                            <div className="title">
                                                {t('map_title')}
                                                <div
                                                    className="close"
                                                    onClick={() => setIsOpen(false)}
                                                    title="닫기"
                                                />
                                            </div>
                                            <div className="body">
                                                <div className="img">
                                                    <img
                                                        src="//t1.daumcdn.net/thumb/C84x76/?fname=http://t1.daumcdn.net/cfile/2170353A51B82DE005"
                                                        width="73"
                                                        height="70"
                                                        alt="넷티스 로고"
                                                    />
                                                </div>
                                                <div className="desc">
                                                    <div className="ellipsis">
                                                        {t('map_address')}
                                                        {/*경기도 수원시 영통구 대학3로 1, 4층 403호*/}
                                                    </div>
                                                    <div className="jibun ellipsis">
                                                        {t('map_address2')}
                                                        {/*(우) 16227 (지번) 이의동 1254-2*/}
                                                    </div>
                                                    <div>
                                                        <a href="https://map.kakao.com/link/to/넷티스,37.299241, 127.042792"
                                                           target="_blank" className="link">{t('map_homepage')}</a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    ;
                                </CustomOverlayMap>
                            )}
                            {/* +- 줌컨트롤 표시 */}
                            <ZoomControl position={kakao.maps.ControlPosition.TOPRIGHT}/>
                            {/* 스카이뷰 표시 */}
                            <MapTypeControl position={kakao.maps.ControlPosition.TOPRIGHT}/>
                            {/* 교통상황 표시 */}
                            <MapTypeId type={kakao.maps.MapTypeId.TRAFFIC}/>
                        </Map>
                        <div className="map_info_wrap">
                            <div className="map_logo">
                                <img className="btn1 map" src={process.env.PUBLIC_URL + '/assets/img/map_logo.png'}
                                     alt=""/>
                            </div>
                            <ul className="map_info">
                                <li>{t('map_address')}</li>
                                <li>{t('phone_number')}</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Company;