import {useEffect} from "react";
import WOW from "wowjs";
import {useTranslation} from "react-i18next";
import {Link} from "react-router-dom";

const NotFound = () => {
    // wow js
    useEffect(() => {
        new WOW.WOW().init();
    }, []);

    const {t} = useTranslation()
    return (
        <div>
            <div className="head_content">
                <div className="main_text_box sub co1">
                    <h3 className="wow fadeIn" data-wow-delay='.2s'>{t('error_section1_tit1')}</h3>
                    <p className="wow fadeIn" data-wow-delay='.4s'>{t('error_section1_p1')}</p>
                    <Link className="wow fadeInUp btn1 mt30" data-wow-delay='.6s'
                          to="/">{t('error_section1_btn1')}</Link>
                </div>
            </div>
            <div className="main_text_box">

            </div>
        </div>
    )
}

export default NotFound