import {useTranslation} from "react-i18next";

const Footer = () => {
    const {t} = useTranslation()
    return (
        <div className="footer">
            <div className="flex ">
                <div className="left wow fadeInLeft" data-wow-delay='.4s'>
                    <div className="logo_box">
                        <img className="img_ab1"
                             src={process.env.PUBLIC_URL + '/assets/img/logo_footer.png'}
                             alt=""/>
                        <p>From the labs behind Red Kite launchpad and several NFT games</p>
                    </div>
                    <ul className="info">
                        <li>{t('footer_1')}</li>
                        <li>{t('footer_2')}</li>
                        <li>{t('footer_3')}</li>
                        <li>{t('footer_4')}</li>
                    </ul>
                    <p>Copyright © 2022 Nettiss All reserved.</p>
                </div>
                <div className="right wow fadeInRight" data-wow-delay='.8s'>
                    <ul>
                        <li>
                            <a href="https://facebook.com/" target="_blank"
                               rel="noopener noreferrer">
                                <img className="img_ab1"
                                     src={process.env.PUBLIC_URL + '/assets/img/ico_facebook.png'}
                                     alt=""/>
                            </a>
                        </li>
                        <li>
                            <a href="https://kakao.com/" target="_blank" rel="noopener noreferrer">
                                <img className="img_ab1"
                                     src={process.env.PUBLIC_URL + '/assets/img/ico_kakao.png'}
                                     alt=""/>
                            </a>
                        </li>
                        <li>
                            <a href="https://instagram.com/" target="_blank"
                               rel="noopener noreferrer">
                                <img className="img_ab1"
                                     src={process.env.PUBLIC_URL + '/assets/img/ico_instagram.png'}
                                     alt=""/>
                            </a>
                        </li>
                        <li>
                            <a href="https://twitter.com/" target="_blank"
                               rel="noopener noreferrer">
                                <img className="img_ab1"
                                     src={process.env.PUBLIC_URL + '/assets/img/ico_twitter.png'}
                                     alt=""/>
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Footer;